
import loadMore from '@/components/loadmore'
import LoadMore from 'vux/src/components/load-more/index.vue'

import {recruiment,deleteById} from '@/api/company.js'
import Confirm from 'vux/src/components/confirm/index.vue'


export default {
components: {
loadMore,
LoadMore,
Confirm
},
created () {
this.id = this.$route.query.id
this.getrecruiment()
},
data () {
return {
historylists: [],
orderId: '',
total: 0,
loading: false,
allLoaded: false,
bottomText: '',
endtext: '',
showdialog: false,
nowId: '',
id: ''
}
},
methods: {

chooseitem (item) {
this.nowId = item.id
this.showdialog = true

},
loadBottom: function() {
if (!this.endtext) {
this.loading = true;
}
this.orderId = this.historylists[this.historylists.length - 1].orderId
if (this.orderId == 0) {
this.loading = false;
this.allLoaded = true;
return;
}
this.getrecruiment()
},
handleBottomChange(val) {
this.bottomText = val;
},
getrecruiment () {
let pdata = {
"pagesize": "10",
"pageLastId": this.orderId,
"userId": this.id
}

recruiment(pdata).then(res => {
if (res.data.result == 0) {
let lists = res.data.datas.list
if (lists.length == 0) {
this.endtext = '再也没有了...'
}
this.total = res.data.datas.total

if (this.orderId == '') {

lists.forEach(item => {
this.historylists.push(item)
})
} else if (this.orderId != 0) {
setTimeout(() => {
this.loading = false;
lists.forEach(item => {
this.historylists.push(item)
})
}, 600)
}

}


})
},
delete_one () {

let data = {
id: this.nowId
}

deleteById(data).then(res => {
if (res.data.result == 0) {
this.orderId = ''
this.historylists = []
this.getrecruiment()
}
})

}
}
}



